.updatePassword{
    min-height: 100vh;
    background-color: rgb(252, 240, 217);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2vmax;
    box-sizing: border-box;
}
.updatePasswordForm{
    background-color: white;
    width: 50%;
    border-radius: 30px;
    box-sizing: border-box;
    padding: 2vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.updatePasswordInputs{
    box-sizing: border-box;
    padding: 1vmax 2vmax;
    width: 80%;
    border-radius: 40px;
    border: 1px solid #ccc;
    margin: 2vmax;
    font: 100 1.2rem "Roboto",sans-serif;
    outline: none;
}

@media screen and (max-width:700px) {
    .updatePasswordForm{
        width: 100%;
    }
    .updatePasswordInputs{
        padding: 4vw 8vw;
        margin: 2vmax;
        font: 100 1.2rem "Roboto",sans-serif;
        margin: 4vw;
    }
}